import React,{Component} from 'react'
import './PartOneBox.styl'
import PartOneItem from './PartOne'
export default class PartOneBox extends Component {
    render (){
        const arr = [1,2,3,4,5,6]
        return (
            <div className='part-one-box'>
                {arr.map(item=>{
                    return  <PartOneItem key={item}></PartOneItem>
                })}
            </div>
        )
    }
}


