import React,{Component} from 'react'
import './Timer.styl'
export default class Timer extends Component{
    constructor(...args){
        super(...args)
        this.state = {
            day:'0',
            hour:'0',
            minute:'0',
            second:'0'
        }
    }
    componentWillMount(){
        this.countDown()
    }
    componentWillUnmount(){
        clearInterval(window.timer);
    }
    countDown(){
        let now = Date.parse(new Date());
        let ElevenDay = Date.parse('2019/1/15');
        let times = (ElevenDay-now)/1000;
        let _this = this;
        window.timer = setInterval(function(){
            let day=0,
                hour=0,
                minute=0,
                second=0;
            if(times > 0){
                day = Math.floor(times / (60 * 60 * 24)).toString();
                hour = (Math.floor(times / (60 * 60)) - (day * 24)).toString();
                minute = (Math.floor(times / 60) - (day * 24 * 60) - (hour * 60)).toString();
                second = (Math.floor(times) - (day * 24 * 60 * 60) - (hour * 60 * 60) - (minute * 60)).toString();
            }
            if (day <= 9) day = ('0' + day).toString();
            if (hour <= 9) hour = ('0' + hour).toString();
            if (minute <= 9) minute = ('0' + minute).toString();
            if (second <= 9) second = ('0' + second).toString();
            // console.log(`还有${day}天${hour}小时${minute}分${second}秒到光棍节`);
            _this.setState({
                 day:day,
                 hour:hour,
                 minute:minute,
                 second:second
             })
            times--;
        },1000);
        if(times<=0){
            clearInterval(window.timer);
        }
    }
    render(){
        const {day,hour,minute,second} = this.state;
        return(
            <div className='timer'>
                <p className='ac'>距离年货节结束还剩：
                {day.split('').map((item,k)=>{
                    return <span key={`day-${k}`}>{item}</span>
                })}
                天
                {hour.split('').map((item,k)=>{
                    return <span key={`hour-${k}`}>{item}</span>
                })}
                小时
                {minute.split('').map((item,k)=>{
                    return <span key={`min-${k}`}>{item}</span>
                })}
                分
                {second.split('').map((item,k)=>{
                    return <span key={`sec-${k}`}>{item}</span>
                })}
                秒</p>
            </div>
        )
    }
}


