import React, { Component } from 'react';
import './partone.styl';
export default class PartOneItem extends Component {
    render(){
        const bg = require('./part1-bg.png')
        return(
            <div className='part-one-item' style={{backgroundImage:"url("+bg+")"}}>
                <img src="http://admin.fufentong.com/media/upload/2018/03/22/5ab32205292ce.jpg" alt="detail"/>
                <p className='part-one-item-tile'>阿道夫洗发套装</p>
                <p className='item-price'>年货节价:<span className='total'>1999</span><span>福分</span></p>
                <a className='part-one-btn'>立即抢购</a>
            </div>
        )
    }
}


