import React,{Component} from 'react'
import { Link } from 'react-router-dom';
import '../stylus/upshot.styl'


class Winning extends Component{
    jumpRoute(route){
        let {history} = this.props;
        history.push(route)
    }
    render(){
        return(
            <div className='upshot'>
                <div className='status-bar'>
                    <a >活动规则</a>
                    <Link to={'/couponlist'} className='fr'>中奖记录</Link>
                </div>
                <p className='Winning-record ac'>中奖啦！恭喜您获得</p>
                <div className='red-envelope'>
                    <p className='prize ac'>免单</p>
                    <div className='use-now ac bold'
                    onClick={this.jumpRoute.bind(this,'/')}
                    >立即使用</div>
                    <p className='envelope-notice ac'>
                    全场无门槛使用（除满减商品外）
                    </p>
                </div>
            </div>
        )
    }
}
class Notwin extends Component {
    jumpRoute(route){
        let {history} = this.props;
        history.push(route)
    }
    render(){
        return(
            <div className='upshot'>
                <div className='status-bar'>
                    <a >活动规则</a>
                    <Link to={'/home'} className='fr'>中奖记录</Link>
                </div>
                <p className='Winning-record ac not'>您今天的红包已领取！</p>
                <div className='not-win-btns'>
                    <div className='tomorrow btn bold ac' onClick={this.jumpRoute.bind(this,'/')}>明天再来</div>
                    <div className='add-chance btn bold ac' onClick={this.jumpRoute.bind(this,'/')}>
                        增加机会
                    </div>
                </div>
                <img className='envelope-img' src="./img/red-envelope-3.png" alt=""/>
            </div>
        )
    }
}

export default class Upshot extends Component {
    constructor(props){
        super(props)
        this.state = {
            winStatus:true
        }
    }
    render(){
        const tap = Math.random();
        console.log(tap)
        return(
            <div>
                {
                    tap>0.6?<Winning history={this.props.history}/>: <Notwin history={this.props.history}/>
                }
            </div>

        )
    }
}


