import React ,{Component} from 'react'
import './PartFourCont.styl'
import PartFourItem from './PartFour'
import PartFiveItem from '../PartFive/PartFive'
export default class PartFourCont extends Component {
    render(){
        const arr = [1,2,3,4,5,6]
        return (
            <div className='part-four-cont'>
                <div className='tile-bar ac'>
                    满1990减1000
                </div>
                <div className='list-1 ac'>
                    <PartFourItem></PartFourItem>
                    <PartFourItem></PartFourItem>
                    <PartFourItem></PartFourItem>
                </div>
                <div className='tile-bar ac'>
                超值精选
                </div>
                <div className='list-2 ac'>
                    {arr.map(item=>{
                        return <PartFiveItem key={item}></PartFiveItem>
                    })}
                </div>
            </div>
        )
    }
}


