import React, { Component } from 'react';
import './PartTwo.styl'
export default class PartTwoItem extends Component {
    render(){
        return(
            <div className='part-two-item'>
                <img src="http://admin.fufentong.com/media/upload/2018/03/22/5ab32205292ce.jpg" alt=""/>
                <div className='part-two-detail'>
                    <p className='tile'>雀巢凯利恬8粒混合装礼盒 混合夹心巧克力</p>
                    <p className='price'>
                    年货节价：<span>488</span>福分
                    </p>
                    <a >立即购买</a>
                </div>
            </div>
        )
    }
}


