import React ,{Component} from 'react'
import './PartTwoContainer.styl'
import PartTwoItem from './PartTwo'
import PartThreeItem from '../PartThree/PartThree'

export default class PartTwoContainer extends Component{
    render(){
        const arr1 = [1,2,3,4]
        return(
            <div className='part-two-container'>
                <div className='head'>
                    <div className='head-tile'>
                        <img className='fl' src="./img/cloud-left.png" alt=""/>
                        零食糖果  买一送一
                        <img className='fr' src="./img/cloud-right.png" alt=""/>
                    </div>
                </div>
                <div className='content'>
                    <div className='list-1 ac'>
                        {arr1.map(item=>{
                            return <PartTwoItem key={item}></PartTwoItem>
                        })}
                    </div>
                    <div className='list-tile'>
                        <img className='fl' src="./img/cloud-left.png" alt=""/>
                        家的味道
                        <img className='fr' src="./img/cloud-right.png" alt=""/>
                    </div>
                    <div className='list-2 ac'>
                        {arr1.map(item=>{
                            return  <PartThreeItem key={item}></PartThreeItem>
                        })}
                    </div>
                </div>
                <div className='foot'>
                </div>
            </div>
        )
    }
}


