import React,{Component} from 'react'
import CouponItem from '../components/CouponItem/CouponItem'
class List extends Component {
    render(){
        const { list } = this.props
        return (
            <div>
                {
                    list.map((item,key)=>{
                        return <CouponItem  key={key} item={item}/>
                    })
                }
            </div>
        )
    }
}
class Nolist extends Component{
    render(){
        return(
            <div className='ac'>
                <p style={{paddingTop:100}}>没有数据   -_-!!</p>
                <a href="/" style={{color:'red',marginTop:50,display:'block'}}>逛逛去吧！</a>
            </div>
        )
    }
}
export default class CouponList extends Component {
    render(){
        const list = [1]
        return (
            <div className='coupon'>
                {
                    list.length===0?<Nolist />:<List list={list}/>  
                }
            </div>
        )
    }
}


