import React,{Component } from 'react'
import './PartThree.styl'
export default class PartThreeItem extends Component {
    
    render(){
        return(
            <div className='part-three-item'>
                <img src="http://admin.fufentong.com/media/upload/2018/03/22/5ab32205292ce.jpg" alt=""/>
                <p className='tile'>
                泰国进口 KOKO 泰国茉莉香米 泰米 泰国大米2kg</p>
                <p className='price ac'>
                年货节价：<span>488</span>福分</p>
                <a >立即抢购</a>
            </div>
        )
    }
}


