import React, { Component } from 'react';
import '../stylus/home.styl'
import PartOneIndex from '../components/PartOne/PartOneIndex'
import PartOneBox from '../components/PartOne/PartOneBox'
import PartTwoContainer from '../components/PartTwo/PartTwoContainer'
import PartFourCont from '../components/PartFour/PartFourCont'
import Envelope from '../components/envelope/envelope'
import Timer from '../components/Timer/Timer'
import storage from '../components/storage'
export default class Home extends Component{
    constructor(props){
        super(props)
        this.state = {
            showEnvelope:true
        }
    }
    openEnvelope(){
        this.setState({
            showEnvelope:false
        })
    }
    componentWillMount(){
        const { addSession,hasItem } = storage;
        if(hasItem('envelope')){
            this.setState({
            showEnvelope:false
            })
        }else{
            console.log(1);
            addSession('envelope',{status:true})
        }
    }
    render(){
        const {showEnvelope} = this.state
        return(
            <div className='home-page'>
                {showEnvelope?<Envelope open={this.openEnvelope.bind(this)}/>:null}
                <img className='head-img' src="./img/head.jpg" alt=""/>
                <div className='part-one'>
                    <Timer></Timer>
                    <PartOneIndex></PartOneIndex>
                    <img className='title-1' src="./img/title-1.png" alt=""/>
                    <PartOneBox></PartOneBox>
                </div>
                <div className='part-two'>
                    <img className='title-2' src="./img/title-2.png" alt=""/>
                    <PartTwoContainer></PartTwoContainer>
                </div>
                <div className='part-three'>
                    <img className='tile-3' src="./img/title-3.png" alt=""/>
                    <PartFourCont></PartFourCont>
                </div>
            </div>
        )
    }
}


