import React,{Component} from 'react' 
import './PartFour.styl'
export default class PartFour extends Component {
    render(){
        const bg = require('./part4-bg.png')
        return(
            <div className='part-four-item' style={{backgroundImage:"url("+bg+")"}}>
                <img 
                className='fl'
                src="http://admin.fufentong.com/media/upload/2018/03/22/5ab32205292ce.jpg" alt=""/>
                <div className='fl'>
                    <p className='tile'>
                    徐福记洽好福礼A-2礼盒坚果干货零食年货大礼包 
                    </p>
                    <p className='price'>
                    年货节价：
                        <span>488</span>
                        福分
                    </p>
                </div>
            </div>
        )
    }
}


