import React ,{Component} from 'react'
import './PartFive.styl'
export default class PartFive extends Component{
    render(){
        const bg = require('./five.jpg')
        return (
            <div className='part-five-item' style={{backgroundImage:"url("+bg+")"}}>
                <img src="http://admin.fufentong.com/media/upload/2018/03/22/5ab32205292ce.jpg" alt=""/>
                <p className='part-one-item-tile'>阿道夫洗发套装阿道夫洗发套装</p>
                <p className='item-price'>年货节价:<span className='total'>1999</span><span>福分</span></p>
                <a className='part-one-btn'>立即抢购</a>
            </div>
        )
    }
}


